<template>
  <div>
    <h1>5월 마지막주 영화 예매 순위</h1>
    <ul>
      <li v-for="(m, i) in movies" class="item" v-bind:key="i">
        <span class="rank">{{m.id}}</span>
        <router-link :to="{ name: 'ProductInfo', params: { id: m.id }}">
          <img v-bind:src="m.poster" class="poster">
        </router-link>
        <div class="detail">
          <strong class="tit">{{m.name}}</strong>
          <span class="rate">예매율 <span class="num">{{m.rate}}</span></span>
          <router-link :to="{ name: 'ProductInfo', params: { id: m.id }}" class="link">자세히보기</router-link>
        </div>
      </li>
    </ul>
  </div>
</template>
<script>
export default {
  created () {
    this.$http.get('/api/products/')
      .then((response) => {
        this.movies = response.data
      })
  },
  data () {
    return {
      movies: []
    }
  }
}
</script>
